<template>
<div>
  <van-submit-bar
    :price='9985'
    button-text="提交订单"
    button-round="true"
    currency="¥"
    @submit="submitFrom" style="margin-bottom: 50px; margin-top: auto">
    <van-check-box :value=selectAll @click="onChange">全选</van-check-box>
  </van-submit-bar>
</div>
</template>

<script>

  import {SubmitBar, Tag,Checkbox} from 'vant';

  export default {
    components: {
      VanSubmitBar: SubmitBar,
      VanTag: Tag,
      VanCheckBox:Checkbox
    },
    imgUrl1: {
      type: Number,
      value: 60.2
    },
    data(){
      return {
        imgUrl: this.imgUrl1,
        selectAll:true
      }
    },
    methods:{
      submitFrom(){
        alert("哈哈哈");
      },
      onChange(){
        this.selectAll=!this.selectAll;

      }
    }
  }
</script>

<style scoped>

</style>



