<template>
  <div>
    <!--  <van-button type="default" @click="printResult()">默认按钮</van-button>-->

    <van-check-box-group v-model="checkResult" :value=checkResult>
      <van-row v-for="(item,index) in orderCopy" :key="item.id">
        <van-col span="2">
          <van-check-box class="checkClass" :name="order[index].id"  @change="onChange($event)"/>
        </van-col>
        <van-col span="21">
          <van-swipe-cell>
            <van-card
              :num=order[index].num
              :price=order[index].price
              :desc=order[index].desc
              :title=order[index].title
              thumb="https://img.yzcdn.cn/vant/ipad.jpeg"
            >
              <div slot="footer">
                <van-stepper id="10086" step="1" :value=order[index].num @change="onChange($event)"/>
              </div>
            </van-card>
            <van-button
              slot="right"
              square
              text="删除"
              type="danger"
              class="delete-button"
            />
          </van-swipe-cell>
        </van-col>
      </van-row>
    </van-check-box-group>
  </div>
</template>

<script>
  import {Card, Button, Checkbox, CheckboxGroup, Cell, Stepper, SwipeCell, Col, Row} from 'vant';

  export default {
    components: {
      VanCard: Card,
      VanButton: Button,
      VanCheckBox: Checkbox,
      VanCheckBoxGroup: CheckboxGroup,
      VanCell: Cell,
      VanStepper: Stepper,
      VanSwipeCell: SwipeCell,
      VanCol: Col,
      VanRow: Row
    },
    data() {
      return {
        imgUrl: './static/ani.ico',
        checkResult: [],
        checkResult1: [],
        totalPrice: 0,
        orderCopy: [],
        order: [{
          id: 10086,
          price: 200,
          num: 1,
          desc: "中国移动",
          title: "限购一件",
        }, {
          id: 10000,
          price: 300,
          num: 1,
          desc: "中国电信",
          title: "限购一件",
        },
          {
            id: 10001,
            price: 200,
            num: 1,
            desc: "中国联通",
            title: "限购一件",
          },
          {
            id: 10002,
            price: 200,
            num: 1,
            desc: "中国联通",
            title: "限购一件",
          }]
      }
    },
    created() {

    },
    mounted() {
      let orderElement = this.order;
      for (let i = 0; i < orderElement.length; i++) {
        let orderCopyTemp = {
          id: 1,
          price: 1,
          num: 1,
          check: true,
          status: 1
        };
        orderCopyTemp.id = orderElement[i].id;
        orderCopyTemp.price = orderElement[i].price;
        orderCopyTemp.num = orderElement[i].num;
        orderCopyTemp.check = true;
        orderCopyTemp.status = 1;
        this.orderCopy.push(orderCopyTemp);
      }
      for (let j = 0; j < this.orderCopy.length; j++) {
        this.checkResult.push(this.orderCopy[j].id);
      }
      //console.log(JSON.stringify(this.orderCopy));
      //console.log(this.checkResult);
    },
    methods: {
      printResult() {
        console.log(this.checkResult)
        //console.log(this.checkResult1)
      },
      onChange(event) {
        //this.order[1].num = event;
        console.log(this.totalPrice);

      },
      onChangeBox(event) {

        console.log(event);
        console.log("---");
      },
      computeTotalPrice(event) {
        //未选中任何商品
        console.log(event);

/*        let orderCopy = this.orderCopy;
        for (let i = 0; i < orderCopy.length; i++) {
          if (orderCopy[i].check === true) {
            this.totalPrice += orderCopy[i].num * orderCopy[i].price;
          }
          console.log(this.orderCopy);
        }
        console.log(this.totalPrice);*/
      }
    },
    watch: {
      orderCopy () {
        //console.log("------"+JSON.stringify(this.orderCopy));
      }
    }

  }

</script>

<style scoped>
  .goods-card {
    margin: 0;
    background-color: white;
  }

  .delete-button {
    height: 100%;
  }

  .checkClass {
    margin-top: 50px;
  }

</style>



